@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&family=Shadows+Into+Light&display=swap);
html {
    background-color: #dda15e;
    font-weight: bold;
}

h1 {
    color: #283618;
    font-family: 'Permanent Marker', cursive;
    text-align: center;
    font-size: 40px;
}

p {
    text-align: center;
    font-size: 26px;
    font-family: 'Shadows Into Light', cursive;
}
h2 {
    color: #283618;
    font-family: 'Permanent Marker', cursive;
    text-align: center;
    font-size: 30px;
}

ul {
    width: 300px;
    margin: 0 auto;
    font-size: 26px;
    font-family: 'Shadows Into Light', cursive;
}

span {
    color: #283618;
    font-family: 'Permanent Marker', cursive;
    font-size: 20px;
}
